import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { fetchToDos, createToDo, deleteToDo, editToDo } from '../api/todo';

const key = 'todos';

export const useGetTodos = () => {
  return useQuery({
    queryKey: [key],
    queryFn: () => fetchToDos(),
  });
};

export const useCreateTodo = () => {
  const queryClient = useQueryClient();

  const mutation = useMutation({
    mutationFn: (newTodo) =>
      createToDo({
        ...newTodo,
      }),
    onSuccess: (todo) => {
      queryClient.setQueryData([key], (prevTodos) => [...prevTodos, todo]);
    },
  });

  return mutation;
};

export const useDeleteTodo = () => {
  const queryClient = useQueryClient();

  const mutation = useMutation({
    mutationFn: (id) => deleteToDo(id),
    onSuccess: (data, id) => {
      queryClient.setQueryData([key], (prevTodos) =>
        prevTodos.filter((todo) => todo.id !== id)
      );
    },
  });

  return mutation;
};

export const useEditTodo = () => {
  const queryClient = useQueryClient();

  const mutation = useMutation({
    mutationFn: (todo) => editToDo(todo.id, todo.title),
    onSuccess: (data, vars, c) => {
      queryClient.setQueryData([key], (prevTodos) =>
        prevTodos.map((prevTodo) =>
          prevTodo.id === data.id ? { ...prevTodo, ...data } : prevTodo
        )
      );
    },
  });

  return mutation;
};
