import React from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import './style.css';
import { Link } from 'react-router-dom';
import AlertTitle from '@mui/material/AlertTitle';
import Alert from '@mui/material/Alert';
import { useGetTodos, useDeleteTodo } from '../../hook/useTodo';

function TodoList() {
  const { isPending, error, data } = useGetTodos();
  const mutation = useDeleteTodo();
  const [activeTodo, setActiveTodo] = React.useState(null);

  if (isPending) {
    return <CircularProgress />;
  }

  if (error) {
    <Alert severity="error">
      <AlertTitle>Ups! it was an error 🚨</AlertTitle>
      Let's <strong>check it out!</strong>
    </Alert>;
  }

  const handleTodoClick = (id) => {
    setActiveTodo(id);
  };

  const handleEditClick = (event, id, title) => {
    // this one was reseting the state
    // event.stopPropagation();

    console.log('handleEditClick', id, title);
  };

  const handleDeleteClick = (event, id) => {
    // careful with this one, it was reseting the state
    // event.stopPropagation();

    mutation.mutate(id);
  };

  return (
    <div className="todo__list">
      <Link to="/create">
        <Button variant="outlined">Add Todo</Button>
      </Link>

      <TableContainer component={Paper}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Todo name</TableCell>
              <TableCell align="left">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data?.map((todo) => (
              <TableRow
                key={todo.id}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                className={`todo ${
                  activeTodo === todo.id ? 'todo--active' : ''
                }`}
                onClick={() => handleTodoClick(todo.id)}
              >
                <>
                  <TableCell>{todo.title}</TableCell>
                  <TableCell align="right">
                    <Stack spacing={2} direction="row">
                      <Link to={`/edit/${todo.id}`}>
                        <Button
                          variant="outlined"
                          onClick={(event) =>
                            handleEditClick(event, todo.id, todo.title)
                          }
                        >
                          Edit
                        </Button>
                      </Link>
                      <Button
                        variant="outlined"
                        onClick={(event) => handleDeleteClick(event, todo.id)}
                      >
                        Delete
                      </Button>
                    </Stack>
                  </TableCell>
                </>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}

export default TodoList;
