import React, { useState } from 'react';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Link } from 'react-router-dom';
import './style.css';
import { useCreateTodo } from '../../hook/useTodo';

function TodoCreate() {
  const [newTodo, setNewTodo] = useState({
    title: '',
    completed: false,
  });
  const mutation = useCreateTodo();

  const handleAddClick = () => {
    mutation.mutate({ title: newTodo.title, completed: newTodo.completed });

    setNewTodo({
      title: '',
      completed: false,
    });
  };

  return (
    <div className="todo__create">
      <Link to="/">
        <Button variant="outlined">Back to todo list</Button>
      </Link>
      <Box
        component="form"
        sx={{
          '& > :not(style)': { m: 1, width: '25ch' },
        }}
        noValidate
        autoComplete="off"
      >
        <TextField
          id="outlined-basic"
          label="Title"
          variant="outlined"
          type="text"
          value={newTodo?.title}
          onChange={(event) =>
            setNewTodo((prev) => {
              return {
                ...prev,
                title: event.target.value,
              };
            })
          }
          autoComplete="off"
          name={`title-${Math.random().toString(36).substring(7)}`}
        />
        <Button variant="outlined" onClick={handleAddClick}>
          Add
        </Button>
      </Box>
    </div>
  );
}

export default TodoCreate;
