import React, { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { Link } from 'react-router-dom';
import { useEditTodo } from '../../hook/useTodo';
import { useParams } from 'react-router-dom';
import Box from '@mui/material/Box';

function TodoEdit() {
  const [editedTodo, setEditedTodo] = useState({
    id: '',
    title: '',
  });
  const params = useParams();
  const { id } = params;

  const mutation = useEditTodo();

  const handleEditInputChange = (value) => {
    setEditedTodo({
      id,
      title: value,
    });
  };

  const handleSaveClick = async (event, id) => {
    console.log('handleSaveClick', id);

    mutation.mutate({ id, title: editedTodo.title });
  };

  if (!id) return null;

  return (
    <div className="todo__edit">
      <p>Edit page</p>
      <span>Edit user {id}</span>
      <Link to="/">
        <Button variant="outlined">Back to todo list</Button>
      </Link>
      <Box
        component="form"
        sx={{
          '& > :not(style)': { m: 1, width: '25ch' },
        }}
        noValidate
        autoComplete="off"
      >
        <TextField
          id={`edit-title-${id}`}
          label="Edit Title"
          variant="outlined"
          type="text"
          value={editedTodo.title}
          onChange={(event) => handleEditInputChange(event.target.value)}
        />

        <Link to="/">
          <Button
            variant="outlined"
            onClick={(event) => handleSaveClick(event, id)}
          >
            Save
          </Button>
        </Link>
      </Box>
    </div>
  );
}

export default TodoEdit;
